.questions-progress {
  width: 60px;
  height: 60px;
  display: flex;
  margin: 0 auto 15px;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 8px 1px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  color: rgba(0, 0, 0, 0.7);
  background: white;
  font-size: 16px;
  font-weight: bold;
  border: 4px solid rgba(0, 0, 0, 0.3);
  position: fixed;
  left: 50%;
  bottom: 5px;
}
.results {
  padding: 10px;
}
.results img {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  object-fit: contain;
}

.results h3 {
  font-weight: bold;
  font-size: 23px;
  text-align: center;
}

.try-again {
  display: flex;
  align-items: center;
  border-radius: 8px;
  text-transform: uppercase;
  text-align: center;
  box-shadow: 0 5px 13px 1px rgba(0, 0, 0, 0.1);
  padding: 15px 30px;
  margin: auto;
  font-size: 17px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  border: 0;
  background: white;
  cursor: pointer;
}

