#question-form .question-input {
  width: 350px;
  padding: 12px;
  border-radius: 4px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid rgba(0, 0, 0, 0.3);
  margin: 0 10px 20px 10px;
  background: white;
}

.question-input:focus {
  box-shadow: 1px 2px 3px 1px rgba(0, 0, 0, 0.4);
  outline: none;
}

.react-tagsinput {
  border: none;
  padding: 0;
  background: transparent;
}

.react-tagsinput-tag {
  background-color: whitesmoke;
  border-radius: 20px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.6);
  font-family: "Lato", sans-serif;
  font-weight: 500;
  padding: 5px 8px 5px 12px;
  text-transform: capitalize;
}

.image-upload {
  width: 380px;
  margin: 0 10px;
  padding: 10px;
  background-color: white;
  font-family: Lato, "sans-serif";
  border-radius: 10px;
  font-size: 14px;
  text-transform: capitalize;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.6);
  letter-spacing: 1px;
  cursor: pointer;
}

.image-area {
  display: flex;
  align-items: center;
}

.image-area p {
  font-size: 12px;
  opacity: 0.7;
}

.submit-area {
  display: flex;
  margin-top: 20px;
}

.submit-button {
  padding: 10px 0;
  width: 100%;
  margin: auto;
  border-radius: 4px;
  border: none;
  background: seagreen;
  color: whitesmoke;
  font-size: 14px;
  font-weight: 500;
  box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.2);
}
